<template>
    <div>
        <form @submit.prevent="">
            <div class="form-group">
                <input v-on:keyup="submitWord" id="inputText" type="text" name="newword" v-model="inputWord" placeholder="Enter word">
            </div>
            <!-- <button class="btn btn-primary" type="submit">Submit</button> -->
        </form>
        <p v-if="error">{{ error }}</p>
    </div>
</template>
<script>
export default {
    name: 'InputWord',
    props: ['newLetter'],
    data(){
        return{
            inputWord: "",
            word: "",
            error: null
        }
    },
    mounted(){
        document.getElementById('inputText').select()
    },
    methods: {
        submitWord() {
            try {
                if(this.inputWord != "" && this.newLetter == this.inputWord.toLowerCase()){
                    this.$emit('success')
                    this.inputWord = ""
                    this.error = null
                    this.$emit('updateWord')
                    document.querySelector('#wordContainer h2').style.display = "none"
                } else if(this.inputWord.length >= this.newLetter.length) {
                    // this.error = "Incorrect!"
                    document.querySelector('#wordContainer h1').style.display = "none"
                } else {
                    // this.error = "Incorrect!"
                }
                document.querySelector('#wordContainer h1').style.display = "block"
                this.highlightLetter()
            } catch (error) {
                console.log(error) 
            }
        },
        randomLetters() {
            if(localStorage.arrletters) {
                let random = Math.floor(Math.random() * 102217)
                let arr = JSON.parse(localStorage.getItem('arrletters'))
                this.newLetter = arr[random]             
            }
        },
        highlightLetter(){
            if(this.inputWord != ""){
                let word = document.querySelector('#wordContainer h1').innerHTML
                let reg = this.inputWord.toLowerCase()
                let regex = new RegExp(reg, "gy")   
                let isCorrect = regex.test(word)         
                if(isCorrect){
                    let result = word.replace(reg, "<span class='highlight'>$&</span>")
                    document.querySelector('#wordContainer h2').innerHTML = result
                    document.querySelector('#wordContainer h1').style.display = "none"
                    document.querySelector('#wordContainer h2').style.display = "block"
                    document.querySelector('#wordContainer h2').style.color = "white"
                    document.querySelector('#wordContainer h2').style.textShadow = "unset"
                } else {
                    document.querySelector('#wordContainer h2').innerHTML = word
                    document.querySelector('#wordContainer h1').style.display = "none"
                    document.querySelector('#wordContainer h2').style.display = "block"
                    document.querySelector('#wordContainer h2').style.color = "#990a0a"
                    document.querySelector('#wordContainer h2').style.textShadow = "1px 1px 6px #fff"
                }
            } else {
                document.querySelector('#wordContainer h2').style.display = "none"
            }
        }
    }
}
</script>
<style scoped>
    .form-group input {margin-bottom: 20px; width: 500px}

    @media only screen and (max-width: 800px) {
        .form-group input {width: 100%}
    }
</style>


