<template>
  <div class="timer-cont">
    <div class="progress-bar"></div>
  </div>
</template>

<script>
export default {
    name: 'Timer',
    data(){
        return {
            
        }
    },
    created() {
    this.barCountdown()
    },
    methods: {
      barCountdown() {
        let width = 100
        let timer = setInterval(()=>{
          width = width - 2
          document.querySelector('.progress-bar').style.width = `${width}px`          
          if(width <=0) {
            clearInterval(timer)
            this.$emit('stopGame')
          }
        },50)
      }
    }
}
</script>

<style>
.progress-bar {width: 1000px; height: 5px; display: block; background: orange; max-width: 100%; margin: 0 auto }
</style>


